html, body {
  padding: 0;
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-family: 'Poppins', sans-serif;
  min-height: 100%;
  background-color: #000;
  background-image: url('./background.jpg');
  // background-position: left 0 bottom calc(-1px * 16vw/1vh);
  background-position: left 0 bottom 0;
  background-attachment: fixed;
  @media (min-aspect-ratio: 1/1) {
    font-size: 10vh;
    @media (min-width: 1600px) {
      font-size: 50px;
    }
  }
  background-repeat: no-repeat;
  background-size: 100% auto;
  color: #EFA860;
  font-size: 10vw;
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

body {
  padding-bottom: 1rem;

  @media (min-aspect-ratio: 1/1) and (min-width: 1600px) {
    align-items: flex-end;
  }
}

a, a:visited, a:active, a:hover {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

.flex-safe-zone {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: inherit;
}

.flex-crumple-zone {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.hero {
  min-height: 100%;
  width: 100%;
  // position: fixed;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @media (min-aspect-ratio: 1/1) and (min-width: 1600px) {
    justify-content: center;
    align-items: center;
    flex: 1 0 auto;
    padding-bottom: 3rem;
    width: 45%;
    margin-right: 5%;
  }

  .content {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;

    @media (min-aspect-ratio: 1/1) and (min-width: 1600px) {
      margin-right: 2.5%;
      padding: 0 2rem;
      max-width: 18rem;
    }

    .hero-title {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      overflow: hidden;

      h1 {
        font-size: 3.5rem;
        margin: 7vh .26rem -0.17rem .26rem;
        padding: .089rem .14rem .17rem .14rem;
        max-width: 100%;
        width: auto;
        overflow: hidden;
        box-sizing: border-box;
        line-height: 0.8;
        color: rgba(0,0,0,0);
        background-image: url('./images/hero.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        font-weight: 600;

        // @media (max-aspect-ratio: 1/1) {
        //   font-size: 10px;
        //   width: 80vw;
        //   height: 30vw;
        //   margin: 6vh 2.6vw -1.7vw 2.6vw;
        // }
      }

      h3 {
        margin: 0.1rem 0.1rem 0.6rem 0.1rem;
        color: #E7DAAC;
        font-size: 0.5rem;
      }
    }

    .social-links {
      border: 0 solid #4A4637;
      border-width: .03rem 0;
      width: 100%;
      box-sizing: border-box;
      background: #1C150A;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      padding: 0.2rem 0.5rem;
      opacity: 0.7;

      @media (min-aspect-ratio: 1/1) and (min-width: 1600px) {
        width: 90%;
        border-width: 0.03rem;
      }

      a{
        margin: 0.2rem;
        height: 1rem;
        width: 1rem;
        display: block;
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
      .twitter {
        background-image: url('./images/socials/twitter2x.png');
      }
      .telegram {
        background-image: url('./images/socials/telegram2x.png');
      }
      .discord {
        background-image: url('./images/socials/discord2x.png');
      }
      .reddit {
        background-image: url('./images/socials/reddit2x.png');
      }
      .tiktok {
        background-image: url('./images/socials/tiktok2x.png');
      }
    }

    .tweets {
      border: 0 solid #4A4637;
      border-width: .03rem 0;
      width: 100%;
      box-sizing: border-box;
      background: #1C150A;

      .tweet {
        padding: .5rem;
        display: flex;
        flex-direction: row;

        .profile-picture {
          height: 1rem;
          width: 1rem;
          border-radius: 50%;
          border: .02rem solid #E7DAAC;
          flex: 0 0 auto;
          margin-right: 0.3rem;
        }

        .tweet-content {
          flex: 1 1 auto;
          font-size: 0.24rem;

          .username-time {
            margin-top: -0.05rem;
            margin-bottom: 0.05rem;

            .username {
              color: #877C5F;
              font-weight: 600;
              &:before {
                content: '@';
              }
            }

            .time {
              color: #594E2F;
              &:before {
                content: ' Â· ';
              }
            }
          }

          .tweet-text {
            color: #E7DAAC;
          }

        }
      }
    }

    .claim-container {
      display: flex;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      padding: 0.3rem;
      margin: 3vh 0;
      flex: 1 1 auto;
      flex-direction: column;
      // flex-wrap: wrap;
      align-content: center;
      justify-content: center;

      h3 {
        font-size: 0.3rem;
        margin: 0rem 0 0.5rem 0;
      }

      button {
        height: 74px;
        line-height: 74px;
        font-size: 27px;
        font-weight: 600;
        border-radius: 37px;
        padding: 0 30px 0 27px;
        color: #EBDFB4;
        background: #291C0C;
        border: 2px solid #957F54;
        cursor: pointer;
        margin: 20px;
      }

      #pricing {
        width: 30vw;
        height: auto;
        padding: 10px;
        margin-top: 10px;

        // @media (min-aspect-ratio: 120/60) {
        //   display: none;
        // }

        // @media (max-height: 940px) {
        //   display: none;
        // }
      }
    }

    .claim-meter {
      height: 0.665rem;
      width: 90%;
      max-width: 100%;
      border: 0.035rem solid #463317;
      border-radius: 0.333rem;
      background: #291C0C;
      box-shadow:
        0 0 0.035rem 0.018rem rgb(0 0 0) inset,
        0 0.070rem 0.158rem 0.035rem rgb(0 0 0 / 40%) inset,
        0 0.088rem 0.070rem 0.210rem #1e1205 inset;
      padding: 0.052rem;
      box-sizing: border-box;
      display: flex;
      flex-direction: row;
      position: relative;
      z-index: 1;

      &:before {
        content: '';
        display: block;
        width: 0.490rem;
        height: 0.490rem;
        border-radius: 0.245rem;
        background: #957F54;
        padding: 0;
        box-shadow:
          -0.035rem -0.052rem 0.123rem 0.052rem #402e14 inset,
          -0.035rem -0.052rem 0.070rem 0.175rem #60410b inset,
          0 0 0.035rem 0 rgba(0,0,0,0.5);
        flex: 1 0 auto;
      }
      .antiprogress {
        flex: 0 1 100%;
      }

      transition: opacity 0.3s, transform 0.4s ease-out, width 0.99s 0.5s ease-in-out;
    }
    .init .claim-meter {
      width: 0.665rem;
      opacity: 0;
      transform: translateY(1.400rem);
    }


    .progress-text {
      font-size: .24rem;
      margin: .10rem;
      transition: opacity 0s 1.2s, transform 0.4s 1.2s ease-out;
    }
    .init .progress-text {
      opacity: 0;
      transform: translateY(-.30rem);
    }

    .disclaimer {
      // width: 30vw;
      padding: 0 0.5rem;
      box-sizing: border-box;
      font-size: 0.20rem;
      text-align: justify;
      // max-height: 30vh;
      // overflow-y: auto;
      flex: 0 0 auto;

      // position: fixed;
      // bottom: 20px;
    }
  }
}

.status-bar {
  font-size: 0.32rem;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: auto;
  line-height: 0.75rem;
  background: rgba(0,0,0,0.7);
  border-top: 0.03rem solid #291C0C;
  padding: 0 0.5rem;
  box-sizing: border-box;
  z-index: 1;

  > * {
    display: none;
  }

  &.connecting {
    display: block;
    &:before {
      content: 'Connecting...';
    }
  }

  &.balance {
    .balance{
      display: inline;
      &:before {
        content: 'Balance: ';
      }
      &:after {
        content: ' SHIHT';
      }
    }
    .address {
      display: inline;
      float: right;
    }
  }

}

.modal-pane {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  background: rgba(0,0,0,0.2);
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
  color: #333;
  font-size: 0.32rem;
  font-weight: 600;

  > div {
    background: #fff;
    flex: 0 0 auto;
    padding: 0.5rem;
    border-radius: 0.5rem;
    overflow: hidden;
    box-shadow: 0 0 0.2rem 0.1rem rgba(0,0,0,0.1) inset;
    max-width: 90vw;
    box-sizing: border-box;

    h3 {
      margin: -0.5rem -0.5rem 0.5rem -0.5rem;
      padding: 0.3rem 0.5rem;
      font-size: 0.36rem;
      text-align: center;
      background: #fff;
      border-bottom: 0.02rem solid #707070;
      box-shadow: 0 -0.1rem 0.2rem 0 rgba(0,0,0,0.1) inset;
    }

    .footer-buttons {
      margin: 0.5rem -0.5rem -0.5rem -0.5rem;
      font-size: 0.36rem;
      text-align: center;
      background: #fff;
      border-top: 0.02rem solid #707070;
      box-shadow: 0 -0.1rem 0.2rem 0 rgba(0,0,0,0.1) inset;
      display: flex;
      flex-direction: row;
      align-items: stretch;
      justify-content: stretch;

      .button {
        padding: 0.3rem 0.5rem;
        flex: 1 1 100%;
        font-size: 0.28rem;
        border-right: 0.02rem solid #707070;
        background: rgba(255,255,255,0.01);

        &:last-child {
          border: 0;
        }

        &:active {
          background: #fff;
          outline: 1px solid #000;
        }
      }
    }
  }
  .modal {display: block;}
}

.modal {display: none;}

#chainModal {

  .swap-icons {
    width: 100%;
    height: auto;
  }

  .chain-names {
    display: flex;
    flex-direction: row;
    font-size: 0.32rem;
    text-align: center;
    font-weight: 600;
    justify-content: space-between;

    div {
      flex: 0 0 45%;
    }
  }

  .chain-arrow {
    fill: #B9B9B9;
    stroke: #707070;
  }

  .chain-logo {
    // fill: url("#chain-from-image");
    stroke: #707070;
    stroke-width: 3px;

  }

  #chain-to-logo {
    // fill: url("#chain-to-image");
  }
}

#txModal {
  .transaction-details {
    width: 100%;
  }

  .transaction-id {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .tx-detail-name {
    color: #666;
    font-weight: normal;
  }
}
